.products {
  padding-bottom: 25px;

  @media (min-width: @tablet-width) {
    padding-bottom: 68px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 75px;
  }
}

.products__list {
  .list-none;

  @media (min-width: @tablet-width) {
    display: grid;
    gap: 60px;
    grid-template-columns: repeat(2, 324px);
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-gap: 62px 80px;
    grid-template-columns: repeat(4, 245px);
  }
}
